import React /*{useState}*/ from 'react'
import LogoLoading from './Logo/index'

import ReactEmbedGist from './Embed/ReactEmbedGist'
import './style.css'
import './theme.css'

// npm i -g gatsby-cli to update
const MyGist = (props) => {

  //let [defaultParam, setDefaultParam] = useState(false)
  ///setDefaultParam(true)


  let defaultCode = "Andr7st/f3274f2ee876783e38c473e7c2672758"
  let defaultP = false
  if(props.paramId === "Andr7st") {
    defaultP = true
  }



  return (
    <div >


      {defaultP && 
      
      <ReactEmbedGist
      gist={defaultCode}
      wrapperClass="gist__bash"
      loadingClass="loading__screen"
      titleClass="gist__title"
      errorClass="gist__error"
      contentClass="gist__content"        
      file="JVA072.java"
      loadingFallback={<LogoLoading />}
      />
      }


    </div>
  )
}

export default MyGist
