import React from 'react'
import Footer from '../views/code/Footer'
import Navbar from '../views/code/Navbar'
import Seo from '../components/SEO/Seo'
import MyGist from '../views/code/myGist/index'


const obtenerParamId = (ruta) => {
  //// 1. Obtener el parametro url: ?id=parametro
  let id = "Andr7st" // default
  let params = new URLSearchParams(ruta.search);
  if(params.get("id")){id=(params.get("id"))}
  return id
}

const code = ({location}) => {
  // let myId = obtenerParamId(location) // parametro de la url

  return (
    <div>
      <Seo title="Test - Develop" />
      <Navbar />
      <h1>0000000000</h1>
      <MyGist paramId={obtenerParamId(location)} />
      <Footer />
    </div>
  )
}

export default code